.timeline {
  width: 100%;
}

.photo {
  margin: 2.5rem 0rem;
  border: 1px solid lightgrey;
  border-radius: 0.3rem;
  width: 100%;
  background-color: #fff;
}


.photo__heading{
  display: flex;
  align-items: center;
  gap: 2rem;
  margin-left: 2rem;
  padding: 1.5rem 0rem;

  img{
    border-radius: 100%;
    width: 3rem;
    height: 3rem;
  }

  a{
    font-size: 1.4rem;
    font-weight: $bold;
    cursor: pointer;
    color: $negro;
  }
}

.comment-icon, .like-icon{
  cursor:pointer;
}

.caption-container--timeline{
  display: flex;
  align-items: center;
  gap: 2rem;
  padding-left: 2rem;

  p{
    font-size: 1.5rem;

  }

  .caption-username--timeline{
    font-weight: $bold;
  }
}

.view-comments{
  cursor: pointer;
  color: darken(lightgray, 2);
  font-size: 1.4rem;
  padding-left: 2rem;
  margin: 0rem 0rem 1rem 0rem;
  
  &:hover{
    color: darken(lightgray, 10)
  }
}