.notifications{
    &__container{
        max-height: 50rem;
        overflow-x: hidden;
        overflow-y: scroll;
        scrollbar-width: thin;
    }

    &__list{
        list-style: none;
    }

    .notification{
        height: 7rem;
        border-radius: 0.5rem; //delete later
        border: 1px solid lightgray; //delete later
        margin: 1rem 0;
    }
}