.add-post-modal--bg {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.85);
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 50;
}

.post-modal--bg {
    position: fixed;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.85);
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 50;

}

.follows-modal{
    position: absolute;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.85);
    width: 100%;
    height: 100%;
    top: 0;
    z-index: 50;
}

.close-modal{
    position: absolute;
    top: 10;
    right: 20;
    cursor: pointer;
}

.add-post-modal {
    position: relative;
    width: 56rem;
    height: 60.2rem;
    border-radius: 1.5rem;
    background-color: $blanco;
}

.post-modal{
    position: relative;
    width: 95rem;
    height: 73rem;
    border-radius: 0.5rem;
    background-color: $blanco;
}

.follows-modal__body{
    position: relative;
    width: 40rem;
    height: 40rem;
    border-radius: 1.5rem;
    background-color: $blanco;
}

.follows-modal-heading{
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid lightgray;
    height: 4.22rem;
}

.follower{
    display: flex;
    align-items: center;
    padding-left: 2rem;
    gap: 2rem;
    margin: 1rem 0rem;

    img{
        width: 3rem;
        height: 3rem;
        border-radius: 100%;
    }

    &__text{
        display: flex;
        flex-direction: column;

        &__username{
            font-size: 1.2rem;
            font-weight: $bold;
            margin: 0;
            color: $negro;
        }
        
        &__alias{
            font-size: 1.2rem;
            margin: 0;
            color: darken(lightgray, 20);
        }
    }

    
}

.add-post-modal--header{
    border-bottom: 1px solid lightgray;
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 4.22rem;

    p{
        font-size: 1.4rem;
        font-weight: 700;
    }

    .next-post-step{
        cursor: pointer;
        color: $azul-primario;
    }

    .arrow-left{
        cursor: pointer;
    }
}

.add-post-modal--body{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 55.98rem;

    p{
        display: block;
        font-size: 2.4rem;
        font-weight: 200;
    }

    .full-width{
        width: 100%;
        height: 55.98rem;
        border-bottom-left-radius: 1.5rem;
        border-bottom-right-radius: 1.5rem;
    }

    .half-post-width{
        width: 55.9rem;
        height: 55.95rem;
    }
    
    .caption-grid{
        grid-template-columns: 1fr 1fr;
    }
    
    .caption-text{
        width: 33.92;
        height: 55.9rem;
    }
}

.post-modal--body{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.post-image-area{
    img{
        height: 73rem;
        border-radius: 0.5rem 0rem 0rem 0.5rem;
    }
}

.img-post{
    border-radius: 0 0 0 1rem;
}

//comments part styling
.post-info{
    padding: 2rem 0rem;
    height: 100%;
}

.post-info__header,
.post-info__header--uploading{
    display: flex;
    align-items: center;
    flex-direction: row;
    gap: 2rem;
    border-bottom: 1px solid lightgray;
    height: 7rem;

    img{
        margin-left: 3rem;
        width: 3.2rem;
        height: 3.2rem;
        border-radius: 100%;
        border: 1px solid lightgray;
    }

    p{
        font-weight: $bold;
        font-size: 1.4rem;
    }
}

.post-info__header--user{
    display: flex;
    align-items: center;
    gap: 2rem;
}

.delete-post{
    position: relative;
}

.delete-post-dropdown{
    text-align: center;
    position: absolute;
    background-color: $blanco;
    width: 10rem;
    top: 30;
    right: -35;
    box-shadow: -1px -2px 15px 0px rgba(37,37,37,0.2); 
}

.delete-post-icon{
    cursor: pointer;
}

.nice-textarea{
    margin-top: 4rem;
    border: none;
    resize: none;
    height: 10rem;
    width: 100%;
    padding-left: 2rem;

    &:focus{
        outline: none;
    }
}

.post-info__comments{
    height: 45rem;
    padding: 1rem;
}

.post-info__management{
    display: flex;
    flex-direction: column;
    border-top: 1px solid lightgray;

    textarea{
        border: none;
        resize: none;
        height: 1.8rem;
        width: 40rem;

        &:focus{
            outline: none;
        }
    }

    &__likes, &__likedby{
        margin-left: 2rem;
        height: 5.5rem;
        display: flex;
        align-items: center;
        gap: 2rem
    }

    &__cform{
        padding-left: 1rem;
        margin: 0.5rem 0rem 0rem 0rem;
        border-top: 1px solid lightgray;
        height: 5.5rem;
        display: flex;
        align-items: center;
        gap: 2rem
    }
}

.post-info__management__likedby{
    width: 38rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.upload-new-post-info{
    display: none;
    width: 100%;
    height: 100%;
}

.caption-container{
    padding-left: 1.8rem;
    margin-bottom: 1rem;
    display: flex;
    align-items: center;
    gap: 1.5rem;

    img{
        width: 3.5rem;
        height: 3.5rem;
        border-radius: 100%;
    }

    .caption-username{
        font-size: 1.4rem;
        font-weight: $bold;
    }

    p{
        font-size: 1.3rem;
    }
}

.comment{
    padding-left: 2rem;
    display: flex;
    align-items: center;
    gap: 1.5rem;

    img{
        width: 3rem;
        height: 3rem;
        border-radius: 100%;
    }

    .comment-username{
        font-size: 1.2rem;
        font-weight: $bold;
    }

    p{
        font-size: 1.2rem;
    }
}

.comments{
    height: 38rem;
    overflow-y: scroll;
}

.submit-comment-btn{
    background-color: transparent;
    cursor: pointer;
    margin-right: 2rem;
    width: 7rem;
    font-size: 1.6rem;
    color: $azul-primario;
    border: none;
}

// upload new profile pic modal styles

.change-prof-pic--bg{
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.85);
    z-index: 50;


    .change-prof-pic--body{
        width: 40rem;
        height: 22.2rem;
        background-color: $blanco;
        border-radius: 1.5rem;

    }
}

.change-prof-pic--heading{
    height: 5rem;
    h3{
        padding: 1rem;
        border-bottom: 1px solid lightgray;
        font-size: 1.8rem;
        margin: 1rem 0;
        font-weight: $bold;
    }
}

.change-prof-pic--content{
    display: flex;
    flex-direction: column;
    align-items: center;

    p{
        text-align: center;
        width: 100%;
        padding: 1.3rem;
        margin: 0;
        font-size: 1.4rem;
        font-weight: $delgada;
        cursor: pointer;
        border-bottom: 1px solid lightgray;
    
        &:last-of-type{
            border-bottom: none;
        }
    }

    .upload-new-prof{
        color: #0095F6;
        font-weight: $bold;
    }
    
    .remove-current-photo{
        color: #ed4956;
        font-weight: $bold;
    }
}

.remove-post{
    color: #ed4956;
    font-weight: $bold;
    cursor: pointer;
} 
    

.liked{
    stroke: #ed4956;
    fill: #ed4956;
}
