.login-container__form, .signup-container__form {
  max-width: 35rem;
  width: 100%;
  margin: 3.2rem 0 1rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 2rem;

  form {
    max-width: 25rem;
    width: 100%;
    gap: 1.5rem;
    display: flex;
    flex-direction: column;
  }

  a {
    font-size: 1.2rem;
  }
}

.login-container__signup, .signup-container__login {
  max-width: 35rem;
  width: 100%;
  margin: 1rem 0 1rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;

  p {
    font-size: 1.4rem;
  }
}

.login-container__functions {
  max-width: 35rem;
  width: 100%;
  margin: 2rem;
}

@include telefono {
  .login-container__form,
  .signup-container__form,
  .login-container__signup,
  .signup-container__login {
    background-color: #fff;
    border: 1px solid lightgray;
  }

  .login-container__form {
    margin-top: 5rem;
  }

  .login-container__form {
    margin-top: 3rem;
  }
}

input {
  background-color: #fafafa;
  border: 1px solid lightgray;
  border-radius: 0.25rem;
  font-size: 1.2rem;
  padding: 1rem;
  width: 25rem;
}

.logo {
  margin-top: 1rem;
  max-width: 20rem;
  width: 100%;
  height: 10rem;
}

//still developing
.cellphones {
  display: none;
  max-width: 45rem;
  max-height: 65rem;
  margin: 3rem 0 0 auto;
  width: 100%;
  height: 100%;

  img {
    margin-left: 10rem;
    width: 40rem;
    height: 55rem;
    border-radius: 5rem;
  }

  img:nth-child(1) {
    position: absolute;
    z-index: 2;
    background-color: #fff;
  }

  img:nth-child(2) {
    z-index: 1;
    border-radius: 5rem;
    position: absolute;
    top: 4rem;
    left: 25rem;
  }
}

@include desktop {
  .cellphones {
    display: block;
  }
}

.boton-blue {
  @include boton($azul-primario, block);
  border-radius: 0.5rem;
  font-size: 1.5rem;
  padding: 0.5rem;
  margin-top: 0.2rem;
}

.boton-blue--variante {
  @include boton($azul-primario, block);
  border-radius: 0.5rem;
  font-weight: $regular;
  font-size: 1.5rem;
  padding: 0.5rem 2rem;
  margin-top: 0.2rem;
}

.app-stores {
  max-width: 35rem;
  width: 100%;
  height: 10rem;
  display: flex;
  flex-direction: column;
  align-items: center;

  p {
    font-size: 1.4rem;
  }

  &__buttons {
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 2rem;
    max-width: 35rem;
    width: 100%;

    img {
      width: 13rem;
      height: 4rem;
    }
  }
}
