/** Media Queries **/
@mixin telefono {
  @media (min-width: #{$telefono}) {
    @content;
  }
}

@mixin tablet {
  @media (min-width: #{$tablet}) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: #{$desktop}) {
    @content;
  }
}
@mixin grid($columnas, $espaciado) {
  display: grid;
  gap: $espaciado;
  grid-template-columns: repeat($columnas, 1fr);
}

@mixin boton($color, $display) {
  display: $display;
  background-color: $color;
  color: $blanco;
  font-weight: $bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: $separacion / 2;
  transition: background-color 0.3s ease;
  border: none;

  &:hover {
    cursor: pointer;
    background-color: darken($color, 5);
  }
}

@mixin boton--pequeno($color, $display){
  display: $display;
  background-color: $color;
  color: $negro;
  font-weight: $bold;
  height: 3rem;
  text-decoration: none;
  padding: 1rem 1rem;
  border-radius: 0.2rem;
  border: 1px solid $gris;
  font-size: 1.4rem;
  align-items: center;

  &:hover {
    cursor: pointer;
  }
}
