.profile{
    &__info{
        margin-top: 10rem;
        display: grid;
        grid-template-columns: 1fr 3fr;
        gap: 5rem;

        img{
            width: 15rem;
            height: 15rem;
            border-radius: 100%;
            padding: 0.5rem;
            border: 2px solid lightgray;
        }

    }

    &__info--header{
        height: 4rem;
        margin-bottom: 2rem;
        display: flex;
        align-items: center;
        gap: 2rem
    }

    &__info--numbers{
        height: 2.5rem;
        display: flex;
        align-items: center;
        gap: 2rem;

        p{
            font-size: 1.6rem;
            font-weight: $delgada;
        
            .bold{
                font-weight: $regular;
            }

            .following-list, .followers-list{
                cursor: pointer;
            }
        }
    }

    &__info--bio{
        height: 12rem;
        line-height: 1;
    }

    &__stories{
        height: 13rem;
        margin: 2rem 0rem;
        display: flex;
        overflow-y: hidden;
        overflow-x: scroll ;
        align-items: center;
        gap: 5rem;
        scrollbar-width: none;
            //images width and height 30rem

        img{
            width: 10rem;
            height: 10rem;
            border-radius: 100%;
            padding: 0.3rem;
            border: 2px solid lightgray;
        }

    }

    &__posts{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 5rem;
        
        &__image{
            position: relative;
            
            img{
                height: 30rem;
                width: 30rem;
                
            }
    
            .overlay{
                position: absolute;
                display: flex;
                align-items: center;
                justify-content: center;
                gap: 1.5rem;
                top: 0;
                height: 30rem;
                width: 30rem;
                background-color: rgba(0, 0, 0, 0);
                cursor: pointer;

                p{
                    display: flex;
                    align-items: center;
                    color: $blanco;
                    font-weight: $bold;
                    visibility: hidden;
                }

                &:hover{
                    background-color: rgba(0, 0, 0, 0.5);

                    p{
                        visibility: visible;
                    }
                }

            }
        }
    }

    &__posts--control{
        position: relative;
        padding: 0rem;
        margin: 2rem 0rem 2rem 0rem;
        height: 5.3rem;
        display: flex;
        justify-content: center;
        text-align: center;
        gap: 3rem;
        
        &::before{
            position: absolute;
            content: '';
            width: 100%;
            height: 0.5px;
            top: -5;
            z-index: -5;
            background-color: lightgray;
        }

        a{
            height: 3rem;
            font-size: 1.4rem;
            text-transform: uppercase;
            color: gray;
            cursor: pointer;
            margin: 1.4rem 0rem;
        }

        
        .posts-active{
            color: $negro;
            font-weight: $bold;
            position: relative;

            &:before{
                position: absolute;
                content: '';
                width: 100%;
                height: 1.5px;
                top: -19;
                background-color: $negro;
            }
        }
    }
}

.bio{
    font-size: 1.4rem;
    font-weight: $regular;
}

.no-post-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    line-height: 1;
    padding-top: 3rem;

    .camera-icon{
        padding: 1rem;
        border-radius: 100% ;
        border: 2px solid $negro;
    }

    h3{
        font-size: 1.8rem;
    }
}
