.card {
  display: none;
}

@include desktop {
  .card {
    display: block;
    max-width: 29.3rem;
    width: 100%;
    grid-column: 2 / 2;
    top: 8.5rem;
    z-index: -1;
    padding-left: 1.5rem;

    .footer {
      align-items: flex-start;
      position: fixed;
      bottom: 170;
    }

    .user-spot {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      position: fixed;
      top: 110;
      gap: 1.5rem;

      img{
        height: 8.5rem;
        width: 8.5rem;
        border-radius: 100%;
        border: 1px solid lightgrey;
        padding: 0.5rem;
      }

      &__info {
        display: flex;
        flex-direction: column;
        font-size: 1.5rem;
        padding-right: 6rem;
      }

      &__change {
        font-size: 1.2rem;
      }
    }

    .instauser {
      font-weight: $bold;
      line-height: normal;
      color: $negro;
      cursor: pointer;
    }
  }

  .suggestions {
    position: fixed;
    bottom: 300;

    &__header {
      display: flex;
      align-items: center;
      justify-content: space-between;

      &__title {
        color: gray;
        font-size: 1.5rem;
      }
    }
  }
}


.suggestions-list{
  list-style: none;
  padding-left: 0;
}

.suggestions-list__li{
  display: flex;
  align-items: center;
  gap: 2rem;
  margin: 1rem 0;

  img{
    width: 2rem;
    height: 2rem;
    border-radius: 100%;
  }

  a{
    color: $negro;
    cursor: pointer;
  }
}