.navbar {
  background-color: #ffffff;
  width: 100%;
  height: 6rem;
  border-bottom: 1px solid lightgray;
  position: fixed;
  top: 0;
  z-index: 20;

  .nav-content {
    max-width: 105rem;
    width: 100%;
    margin: 0 auto;
    max-height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .logo {
    height: 6rem;

    img{
      width: 12rem;
      height: 6rem;
    }
  }

  .search {
    display: none;
  }

  @include tablet {
    .search {
      display: block;
      width: 25rem;
      height: 4rem;
      border-radius: 1rem;
      font-size: 1.8rem;
      background-color: #efefef;
      padding-left: 3rem;
      color: gray;
      border: none;
      background-size: 2rem;
      background-repeat: no-repeat;
      background-position: left center;
    }
  }

  .show-tablet {
    display: none;
  }

  @include telefono {
    .show-tablet {
      display: block;
    }

    .show-mobile {
      display: none;
    }
  }

  .search:focus {
    background-image: none;
    padding-left: 1rem;
    background-size: 2rem;
    background-repeat: no-repeat;
    background-position: right center;
  }

  .icons {
    display: flex;
    gap: 1.5rem;
    padding-right: 2rem;
  }
}

.profile-icon{
  display: flex;
  align-items: center;
  width: 3rem;
  height: 3rem;
  padding: 0.5rem;
  border-radius: 100%;
  cursor: pointer;
  position: relative;

  .dropdown-content{
    position: absolute;
    top: 30;
    right: -10;
    border-radius: 0.5rem;
    margin-top: 1rem;
    background-color: $blanco;
    padding: 1rem;
    max-width: 20rem;
    width: 20rem;
    box-shadow: -1px -2px 15px 0px rgba(37,37,37,0.2); 
    z-index: 5;

    a{
      color: $negro;
      display: flex;
      align-items: center ;
      gap: 0.5rem;
      
      &:last-of-type{
        border-top: 1px solid lightgray;
        margin-top: 0.7rem;
      }
    }

    &::before{
      content: '';
      width: 1.5rem;
      height: 1.5rem;
      top: -7;
      right: 12;
      z-index: -10;
      position: absolute;
      transform: rotate(45deg);
      background-color: $blanco;
      box-shadow: -1px -2px  rgba(37,37,37,0.05); 
    }
  }
}

.profile-icon--btn{
  img{
    width: 3rem;
    height: 3rem;
    padding: 0.2rem;
    border-radius: 100%;
    border: 1px solid lightgray;
  }
}

.add-post{
  cursor: pointer;
}

.nav-buttons{
  display: flex;
  align-items: center;
  gap: 3rem;

  .signup-link{
    color: $azul-primario;
  }
}

.search-container{
  position: relative;
}

.search-dropdown{
  background-color: $blanco;
  width: 25rem;
  height: 15rem;
  position: absolute;
  top: 45;
  box-shadow: -1px 1px 10px 0px rgba(37,37,37,.2);
  overflow-y: scroll;

  ul{
    list-style: none;
  }

}

.search-link{
  color: $negro;
}

.search-user-img{
  width: 3rem;
  height: 3rem;
  border-radius: 100%;

}

.search-list__li{
  display: flex;
  gap: 2rem;
  margin: 1rem 0;
}

.close-search{
  position: absolute;
  top: 10;
  right: 10;
  cursor: pointer;
}