.content {
  .home {
    max-width: 93.5rem;
    width: 100%;
    margin: 6rem auto 5rem auto;
    display: flex;
    justify-content: center;
    padding-top: 2.5rem;

    @include desktop {
      display: grid;
      grid-template-columns: 1fr 0.5fr;
      column-gap: 3rem;
      grid-auto-flow: column;
    }
  }

  .login {
    max-width: 93.5rem;
    width: 100%;
    margin: 0 auto 5rem auto;
    display: flex;
    justify-content: center;

    @include desktop {
      @include grid(2, 1rem);
    }
  }

  .signup{
    max-width: 34.5rem;
    width: 100%;
    margin: 0 auto 5rem auto;
  }

  .profile{
    max-width: 100rem;
    margin: 6rem auto;
  }
  
  .notifications, .follows{
    max-width: 100rem;
    margin: 10rem auto 5rem auto;
  }

  .settings{
    max-width: 93.5rem;
    margin: 10rem auto 5rem auto;
  }
}
