.settings{
    height: 84rem;
    background-color: $blanco;
    border: 1px solid lightgray;
    border-radius: 0.5rem;
    display: grid;
    grid-template-columns: 0.68fr 2fr;

    &__nav{
        border-right: 1px solid lightgray;
        display: flex;
        flex-direction: column;

        a{
            padding: 2rem 0rem 2rem 3rem;
            display: block;
            color: $negro;
            
            &:hover{
                border-left: 1.5px solid lightgray;
                background-color: darken($blanco, 2);
            }
        }

        .active{
            border-left: 2px solid black;
            font-weight: $bold;

            &:hover{
                border-left: 2px solid black;
                background-color: $blanco;
            }
        }
    }
    
    &__form-container{
        padding: 4rem 8rem 2rem 8rem;
    }
}

.user-small-info{
    display: flex;
    align-items: center;
    gap: 2rem;
    margin-bottom: 4rem;

    img{
        width: 3.8rem;
        height: 3.8rem;
        border-radius: 100%;
        border: 1px solid lightgray;
    }

    p{
        margin: 0rem 0rem 1.5rem 0rem;
    }

    a{
        color: $azul-primario;
        font-size: 1.4rem;
    }

    &__text{
        display: flex;
        flex-direction: column;
        line-height: 0.5;
    }
}

.edit-profile-form, .edit-password-form{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 3rem;
}

.edit-profile-form__field{
    display: flex;
    width: 100% ;
    align-items: center;
    gap: 2rem;
    
    .edit-profile-form__field__label {
        flex: 0 0 9rem;
        text-align: right;
        font-weight: $bold;

    }

    .edit-profile-form__field__label--password{
        flex: 0 0 12rem;
    }
    
    .edit-profile-form__field__input{
        flex: 1;
        background-color: $blanco;
        font-size: 1.6rem;
    }    
    .edit-profile-form__field__input--password{
        background-color: $fondo;
    }

    textarea{
        flex: 1;
        height: 6rem;
        resize: none;
    }

    &:last-of-type{
        align-items: flex-start;
    }
}